import { Currency, CurrencyRates } from '../types';

const API_URL = 'https://open.er-api.com/v6/latest';

// Cache for currency rates
let cachedRates: CurrencyRates | null = null;
let cacheTimestamp = 0;
const CACHE_LIFETIME = 3600000; // 1 hour in milliseconds

/**
 * Fetches currency exchange rates from the API
 * @param baseCurrency The base currency to get rates for
 * @returns Promise with currency rates data
 */
export const fetchCurrencyRates = async (baseCurrency: Currency = 'USD'): Promise<CurrencyRates> => {
  // Check if we have valid cached data
  const now = Date.now();
  if (cachedRates && cachedRates.base === baseCurrency && now - cacheTimestamp < CACHE_LIFETIME) {
    return cachedRates;
  }

  try {
    const response = await fetch(`${API_URL}/${baseCurrency}`);
    
    if (!response.ok) {
      throw new Error('Failed to fetch currency rates');
    }
    
    const data = await response.json();
    
    // Format data to match our interface
    cachedRates = {
      base: baseCurrency,
      rates: {
        USD: data.rates.USD || 1,
        EUR: data.rates.EUR || 1,
        GBP: data.rates.GBP || 1,
        INR: data.rates.INR || 1
      },
      timestamp: now
    };
    
    cacheTimestamp = now;
    return cachedRates;
  } catch (error) {
    console.error('Error fetching currency rates:', error);
    
    // Return default values if API fails
    return {
      base: baseCurrency,
      rates: {
        USD: 1,
        EUR: 0.93,
        GBP: 0.79,
        INR: 83.06
      },
      timestamp: now
    };
  }
};

/**
 * Converts an amount from one currency to another
 * @param amount The amount to convert
 * @param fromCurrency The currency to convert from
 * @param toCurrency The currency to convert to
 * @param rates Optional currency rates object (will fetch if not provided)
 * @returns Promise with the converted amount
 */
export const convertCurrency = async (
  amount: number,
  fromCurrency: Currency,
  toCurrency: Currency,
  rates?: CurrencyRates
): Promise<number> => {
  // If currencies are the same, no conversion needed
  if (fromCurrency === toCurrency) {
    return amount;
  }
  
  // Use provided rates or fetch them
  const ratesData = rates || await fetchCurrencyRates(fromCurrency);
  
  // Calculate the converted amount
  return amount * ratesData.rates[toCurrency];
};

/**
 * Get currency symbol for display
 * @param currency The currency code
 * @returns The currency symbol
 */
export const getCurrencySymbol = (currency: Currency): string => {
  switch (currency) {
    case 'USD':
      return '$';
    case 'EUR':
      return '';
    case 'GBP':
      return '';
    case 'INR':
      return '';
    default:
      return '$';
  }
};

/**
 * Format amount with currency symbol
 * @param amount The amount to format
 * @param currency The currency code
 * @returns Formatted amount with currency symbol
 */
export const formatCurrency = (amount: number, currency: Currency): string => {
  const symbol = getCurrencySymbol(currency);
  
  // Remove leading zeros
  const formattedAmount = parseFloat(amount.toString());
  
  // Format with 2 decimal places and ensure the currency symbol is displayed before the amount
  return `${symbol}${formattedAmount.toLocaleString(undefined, { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;
}; 