import { Expense, ExpenseCategory } from '../types';

// Interface for fraud detection results
export interface FraudDetectionResult {
  isFraud: boolean;
  reasons: string[];
}

// Interface for OCR extraction results
export interface OcrExtractionResult {
  text: string;
  vendor?: string;
  amount?: number;
  date?: string;
  fraudDetection: FraudDetectionResult;
}

// Analyze extracted text for missing details
export const detectMissingDetails = (text: string): string[] => {
  const reasons: string[] = [];
  const lines = text.split('\n').filter(line => line.trim().length > 0);
  
  // Check for minimum text length (likely invalid receipt)
  if (text.length < 20) {
    reasons.push('Receipt text is too short or unclear');
  }
  
  // Check for vendor name (usually at the top of receipt)
  if (lines.length === 0 || lines[0].trim().length < 2) {
    reasons.push('Missing vendor information');
  }
  
  // Check for amount
  const amountMatch = text.match(/\$?\s?(\d+[,.]\d{2})/);
  if (!amountMatch || !amountMatch[1]) {
    reasons.push('Missing amount in receipt');
  }
  
  // Check for date
  const dateMatch = text.match(/(\d{1,2}[/-]\d{1,2}[/-]\d{2,4})/);
  if (!dateMatch || !dateMatch[1]) {
    reasons.push('Missing date in receipt');
  }
  
  return reasons;
};

// Check for suspiciously high amounts
export const detectHighAmount = (
  amount: number,
  category: ExpenseCategory,
  previousExpenses: Expense[]
): string[] => {
  const reasons: string[] = [];
  
  // Get previous expenses in the same category
  const categoryExpenses = previousExpenses.filter(exp => exp.category === category);
  
  if (categoryExpenses.length > 0) {
    // Calculate average amount for this category
    const totalAmount = categoryExpenses.reduce((sum, exp) => sum + exp.amount, 0);
    const avgAmount = totalAmount / categoryExpenses.length;
    
    // Flag if amount is significantly higher than average (3x)
    if (amount > avgAmount * 3) {
      reasons.push(`Amount is ${(amount / avgAmount).toFixed(1)}x higher than average for ${category}`);
    }
    
    // Flag if amount is the highest ever for this category
    const maxAmount = Math.max(...categoryExpenses.map(exp => exp.amount));
    if (amount > maxAmount * 1.5) {
      reasons.push(`Amount exceeds highest previous ${category} expense by ${((amount - maxAmount) / maxAmount * 100).toFixed(0)}%`);
    }
  }
  
  // Check for unusually high absolute amounts
  const highThresholds: Record<ExpenseCategory, number> = {
    'Office Supplies': 2000,
    'Food & Entertainment': 5000,
    'Travelling': 10000,
    'Accommodation': 15000,
    'Client & Project Expenses': 25000,
    'Subscriptions': 5000
  };
  
  if (amount > highThresholds[category]) {
    reasons.push(`Amount exceeds typical limit for ${category}`);
  }
  
  return reasons;
};

// Check for duplicate receipts
export const detectDuplicateReceipt = (
  text: string,
  previousReceipts: string[]
): { isDuplicate: boolean; similarity: number; matchedReceipt?: string } => {
  // Normalize text for comparison
  const normalizeText = (input: string): string => {
    return input
      .toLowerCase()
      .replace(/\s+/g, ' ')
      .replace(/[^\w\s]/g, '')
      .trim();
  };
  
  const normalizedCurrent = normalizeText(text);
  
  // Check for suspicious words
  if (normalizedCurrent.includes('copy') || normalizedCurrent.includes('duplicate')) {
    return { 
      isDuplicate: true, 
      similarity: 1.0,
      matchedReceipt: 'Contains "copy" or "duplicate" keywords'
    };
  }
  
  // Check similarity against previous receipts
  let highestSimilarity = 0;
  let matchedReceipt = '';
  
  for (const receipt of previousReceipts) {
    const normalizedPrevious = normalizeText(receipt);
    
    // Skip if too different in length (quick filter)
    if (Math.abs(normalizedCurrent.length - normalizedPrevious.length) / Math.max(normalizedCurrent.length, normalizedPrevious.length) > 0.5) {
      continue;
    }
    
    // Calculate similarity using words in common
    const currentWords = normalizedCurrent.split(' ');
    const previousWords = normalizedPrevious.split(' ');
    
    const commonWords = currentWords.filter(word => previousWords.includes(word));
    const similarity = commonWords.length / Math.max(currentWords.length, previousWords.length);
    
    if (similarity > highestSimilarity) {
      highestSimilarity = similarity;
      matchedReceipt = receipt;
    }
  }
  
  // Consider it a duplicate if similarity is above threshold
  return {
    isDuplicate: highestSimilarity > 0.7,
    similarity: highestSimilarity,
    matchedReceipt: highestSimilarity > 0.7 ? matchedReceipt : undefined
  };
};

/**
 * Simplified fraud detection for expense receipts
 * In a real app, this would use more sophisticated ML/AI techniques
 */
export const detectFraudInReceipt = (
  receiptText: string,
  amount: number | null,
  category: ExpenseCategory,
  userExpenses: Expense[],
  previousReceipts: string[]
): FraudDetectionResult => {
  const result: FraudDetectionResult = {
    isFraud: false,
    reasons: []
  };

  // Skip intensive checks for demo
  if (!receiptText || receiptText.length < 20) {
    result.isFraud = true;
    result.reasons.push('Receipt text is too short or missing');
    return result;
  }

  // Check for high amounts
  if (amount) {
    const typicalMaxAmounts: Record<ExpenseCategory, number> = {
      'Office Supplies': 5000,
      'Food & Entertainment': 2000,
      'Travelling': 10000,
      'Accommodation': 8000,
      'Client & Project Expenses': 20000,
      'Subscriptions': 5000
    };

    if (amount > typicalMaxAmounts[category] * 1.5) {
      result.isFraud = true;
      result.reasons.push(`Amount is unusually high for ${category} category`);
    }
  }

  // For demo, add some randomness to simulate fraud detection
  if (Math.random() < 0.05) {
    result.isFraud = true;
    result.reasons.push('Suspicious pattern detected in receipt text');
  }

  return result;
}; 