/**
 * Simulated OCR functionality
 * In a real app, this would connect to a proper OCR service
 */

// Redefine the fraud detection result interface locally
export interface FraudDetectionResult {
  isFraud: boolean;
  reasons: string[];
}

export interface OCRExtractionResult {
  amount: number | null;
  date: string | null;
  vendor: string | null;
  fraudDetection: FraudDetectionResult;
}

/**
 * Simulates OCR processing on an image file
 * Returns fake extracted text for demo purposes
 */
export const performOCR = async (file: File): Promise<string> => {
  // Simulate processing delay
  await new Promise(resolve => setTimeout(resolve, 200));
  
  // For demo, return fake receipt text
  return `INVOICE
    Store: ABC Supplies Ltd.
    Date: ${new Date().toLocaleDateString()}
    
    Item 1: Office Chair - 4500.00
    Item 2: Desk Lamp - 899.00
    
    Subtotal: 5399.00
    Tax: 971.82
    
    Total: 6370.82
    
    Payment Method: Credit Card
    Transaction ID: TXN-${Math.floor(Math.random() * 10000)}`;
};

/**
 * Extract structured information from OCR text
 */
export const extractInfoFromOcrText = (text: string): OCRExtractionResult => {
  // In a real app, use regex or ML to extract information
  // This is a simplified mock implementation
  
  // Extract amount (finding a pattern like "Total: 1234.56")
  const amountMatch = text.match(/Total:\s*?(\d+(\.\d+)?)/i);
  const amount = amountMatch ? parseFloat(amountMatch[1]) : null;
  
  // Extract date
  const dateMatch = text.match(/Date:\s*([^\n]+)/i);
  const date = dateMatch ? dateMatch[1].trim() : null;
  
  // Extract vendor
  const vendorMatch = text.match(/Store:\s*([^\n]+)/i);
  const vendor = vendorMatch ? vendorMatch[1].trim() : null;
  
  // Basic fraud detection
  // Real implementation would use ML/AI for this
  const fraudDetection = {
    isFraud: false,
    reasons: [] as string[]
  };
  
  // For demo: simulate random fraud detection
  if (Math.random() < 0.1) {
    fraudDetection.isFraud = true;
    fraudDetection.reasons.push('Suspicious formatting detected in receipt');
  }
  
  return {
    amount,
    date,
    vendor,
    fraudDetection
  };
};

// Convert file to base64 (helper function)
export const fileToBase64 = (file: File): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => resolve(reader.result as string);
    reader.onerror = error => reject(error);
  });
}; 