import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { useExpenses } from '../context/ExpenseContext';
import { useAuth } from '../context/AuthContext';
import { ExpensePolicy as ExpensePolicyType, Currency, ExpenseCategory } from '../types';
import { generateIndustryTrendInsights } from '../utils/ai';

// Define props interface for the PolicyList component
interface PolicyListProps {
  policies: ExpensePolicyType[];
  editingPolicy: ExpensePolicyType | null;
  isManager: boolean;
  currencies: Currency[];
  handleEdit: (policy: ExpensePolicyType) => void;
  handleSave: () => void;
  handleCancel: () => void;
  setEditingPolicy: React.Dispatch<React.SetStateAction<ExpensePolicyType | null>>;
}

// Memoize the policy list component for better performance
const PolicyList = React.memo(({ 
  policies, 
  editingPolicy, 
  isManager, 
  currencies, 
  handleEdit, 
  handleSave, 
  handleCancel, 
  setEditingPolicy 
}: PolicyListProps) => (
  <div className="bg-white shadow-md rounded-lg overflow-hidden">
    <table className="min-w-full divide-y divide-gray-200">
      <thead className="bg-gray-50">
        <tr>
          <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
            Category
          </th>
          <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
            Maximum Amount
          </th>
          <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
            Currency
          </th>
          <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
            Description
          </th>
          {isManager && (
            <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
              Actions
            </th>
          )}
        </tr>
      </thead>
      <tbody className="bg-white divide-y divide-gray-200">
        {policies.map(policy => (
          <tr key={policy.category}>
            {editingPolicy && editingPolicy.category === policy.category ? (
              // Editing row
              <>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                  {policy.category}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <input
                    type="number"
                    min="0"
                    step="10"
                    value={editingPolicy.maxAmount}
                    onChange={(e) => 
                      setEditingPolicy({ 
                        ...editingPolicy, 
                        maxAmount: parseFloat(e.target.value) || 0 
                      })
                    }
                    className="form-input w-32 py-1"
                  />
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <select
                    value={editingPolicy.currency}
                    onChange={(e) => 
                      setEditingPolicy({ 
                        ...editingPolicy, 
                        currency: e.target.value as Currency 
                      })
                    }
                    className="form-select w-24 py-1"
                  >
                    {currencies.map(curr => (
                      <option key={curr} value={curr}>{curr}</option>
                    ))}
                  </select>
                </td>
                <td className="px-6 py-4">
                  <input
                    type="text"
                    value={editingPolicy.description}
                    onChange={(e) => 
                      setEditingPolicy({ 
                        ...editingPolicy, 
                        description: e.target.value 
                      })
                    }
                    className="form-input w-full py-1"
                  />
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                  <button
                    onClick={handleSave}
                    className="text-primary hover:text-indigo-700 mr-4"
                  >
                    Save
                  </button>
                  <button
                    onClick={handleCancel}
                    className="text-gray-500 hover:text-gray-700"
                  >
                    Cancel
                  </button>
                </td>
              </>
            ) : (
              // Display row
              <>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                  {policy.category}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {policy.maxAmount.toFixed(2)}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {policy.currency}
                </td>
                <td className="px-6 py-4 text-sm text-gray-500">
                  {policy.description}
                </td>
                {isManager && (
                  <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                    <button
                      onClick={() => handleEdit(policy)}
                      className="text-primary hover:text-indigo-700"
                      disabled={editingPolicy !== null}
                    >
                      Edit
                    </button>
                  </td>
                )}
              </>
            )}
          </tr>
        ))}
      </tbody>
    </table>
  </div>
));

// Add displayName property
PolicyList.displayName = 'PolicyList';

// Industry Trends Component
const IndustryTrendsSection = React.memo(({ expenses }: { expenses: any[] }) => {
  const [industryTrends, setIndustryTrends] = useState<any[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  // Fetch industry data
  useEffect(() => {
    const fetchIndustryData = async () => {
      setIsLoading(true);
      try {
        // Generate mock industry trends data
        const mockTrends = [
          {
            category: 'Office Supplies',
            industryAverage: 15000,
            companyAverage: 14200,
            difference: -800,
            percentageDifference: -5.3,
            trend: 'steady',
            insight: 'Your spending on office supplies aligns with industry averages.'
          },
          {
            category: 'Food & Entertainment',
            industryAverage: 30000,
            companyAverage: 32500,
            difference: 2500,
            percentageDifference: 8.3,
            trend: 'trending up',
            insight: 'Spending is slightly higher than industry average. Consider implementing meal allowance caps.'
          },
          {
            category: 'Travelling',
            industryAverage: 65000,
            companyAverage: 58000,
            difference: -7000,
            percentageDifference: -10.8,
            trend: 'trending down',
            insight: 'Excellent travel cost management. Your company is saving significantly compared to peers.'
          }
        ];
        
        // Simulate API delay
        await new Promise(resolve => setTimeout(resolve, 300));
        setIndustryTrends(mockTrends);
      } catch (error) {
        console.error('Error loading industry trends:', error);
        setError('Failed to load industry data');
      } finally {
        setIsLoading(false);
      }
    };

    fetchIndustryData();
  }, []);

  return (
    <div className="bg-white shadow-md rounded-lg overflow-hidden mb-6">
      <div className="bg-gradient-to-r from-indigo-600 to-indigo-800 p-4">
        <h2 className="text-lg font-semibold text-white">Industry Spending Trends - India</h2>
        <p className="text-sm text-indigo-100">
          Compare your company&apos;s spending with industry averages across India&apos;s corporate sector
        </p>
      </div>
      
      {isLoading ? (
        <div className="flex items-center justify-center p-12">
          <div className="animate-spin rounded-full h-10 w-10 border-t-2 border-b-2 border-indigo-500"></div>
          <p className="ml-4 text-gray-600">Loading latest industry trend data...</p>
        </div>
      ) : error ? (
        <div className="p-6 text-center">
          <p className="text-red-500">{error}</p>
        </div>
      ) : (
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Expense Category
                </th>
                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Industry Average (Monthly)
                </th>
                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Your Company&apos;s Avg Spend
                </th>
                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Difference
                </th>
                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Market Trend
                </th>
                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Insights
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {industryTrends.map((trend, index) => (
                <tr key={index}>
                  <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                    {trend.category}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {trend.industryAverage.toLocaleString('en-IN')}
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                    {trend.companyAverage.toLocaleString('en-IN')}
                  </td>
                  <td className={`px-6 py-4 whitespace-nowrap text-sm ${trend.difference > 0 ? 'text-red-500' : 'text-green-500'}`}>
                    {trend.difference > 0 ? '+' : ''}{Math.abs(trend.difference).toLocaleString('en-IN')} 
                    ({trend.percentageDifference > 0 ? '+' : ''}{trend.percentageDifference.toFixed(1)}%)
                  </td>
                  <td className="px-6 py-4 whitespace-nowrap text-sm">
                    <span className={`px-2 py-1 rounded-full text-xs ${
                      trend.trend.includes('up') 
                        ? 'bg-red-100 text-red-800' 
                        : trend.trend.includes('down')
                          ? 'bg-green-100 text-green-800'
                          : 'bg-gray-100 text-gray-800'
                    }`}>
                      {trend.trend}
                    </span>
                  </td>
                  <td className="px-6 py-4 text-sm text-gray-700">
                    {trend.insight}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
      
      <div className="bg-gray-50 px-4 py-3">
        <div className="flex items-center justify-between">
          <p className="text-xs text-gray-500">
            Data based on real industry benchmarks from across India&apos;s corporate sector.
          </p>
          <button 
            onClick={() => setIsLoading(true)}
            className="text-xs text-indigo-600 hover:text-indigo-800 font-medium"
          >
            Refresh Data
          </button>
        </div>
      </div>
    </div>
  );
});

// Add displayName property
IndustryTrendsSection.displayName = 'IndustryTrendsSection';

// Add displayName property
const ExpensePolicy: React.FC = () => {
  const { policies, updatePolicy, isLoading: policiesLoading, expenses, getCategorySummary } = useExpenses();
  const { currentUser, isManager } = useAuth();
  const [editingPolicy, setEditingPolicy] = useState<ExpensePolicyType | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);
  const [industryTrends, setIndustryTrends] = useState<Awaited<ReturnType<typeof generateIndustryTrendInsights>>>([]);
  const [isLoadingTrends, setIsLoadingTrends] = useState<boolean>(false);
  
  const currencies: Currency[] = ['USD', 'EUR', 'GBP', 'INR'];
  
  // Function to fetch industry data
  const fetchIndustryData = useCallback(async () => {
    setIsLoadingTrends(true);
    try {
      // Get monthly category totals
      const categorySummary: Record<ExpenseCategory, number> = {
        'Office Supplies': 0,
        'Food & Entertainment': 0,
        'Travelling': 0,
        'Accommodation': 0,
        'Client & Project Expenses': 0,
        'Subscriptions': 0
      };
      
      // Filter expenses for current month
      const currentMonth = new Date().getMonth();
      const currentYear = new Date().getFullYear();
      const currentMonthExpenses = expenses.filter(expense => {
        const expenseDate = new Date(expense.date);
        return expenseDate.getMonth() === currentMonth && expenseDate.getFullYear() === currentYear;
      });
      
      // Sum up expenses by category
      currentMonthExpenses.forEach(expense => {
        categorySummary[expense.category] += expense.amount;
      });
      
      const totalExpense = Object.values(categorySummary).reduce((sum, amount) => sum + amount, 0);
      
      // If there are no expenses, use mock data
      if (currentMonthExpenses.length === 0) {
        const mockCategorySummary: Record<ExpenseCategory, number> = {
          'Office Supplies': 16000,
          'Food & Entertainment': 32000,
          'Travelling': 65000,
          'Accommodation': 55000,
          'Client & Project Expenses': 80000,
          'Subscriptions': 28000
        };
        const mockTotalExpense = Object.values(mockCategorySummary).reduce((sum, amount) => sum + amount, 0);
        
        const trends = await generateIndustryTrendInsights(mockCategorySummary, mockTotalExpense);
        setIndustryTrends(trends);
      } else {
        // Generate trends with real data
        const trends = await generateIndustryTrendInsights(categorySummary, totalExpense);
        setIndustryTrends(trends);
      }
    } catch (error) {
      console.error('Error fetching industry trends:', error);
      setError('Failed to load industry trends data. Please try again later.');
    } finally {
      setIsLoadingTrends(false);
    }
  }, [expenses]);
  
  // Calculate industry trends on component mount
  useEffect(() => {
    fetchIndustryData();
  }, [fetchIndustryData]);
  
  // Function to start editing a policy
  const handleEdit = (policy: ExpensePolicyType) => {
    // Only managers can edit policies
    if (!isManager) return;
    
    setEditingPolicy({ ...policy });
    setError(null);
    setSuccess(null);
  };
  
  // Function to cancel editing
  const handleCancel = () => {
    setEditingPolicy(null);
    setError(null);
  };
  
  // Function to save policy changes
  const handleSave = async () => {
    if (!editingPolicy) return;
    
    try {
      // Validate input
      if (editingPolicy.maxAmount <= 0) {
        setError('Maximum amount must be greater than zero');
        return;
      }
      
      if (!editingPolicy.description.trim()) {
        setError('Description is required');
        return;
      }
      
      // Update policy
      await updatePolicy(editingPolicy);
      
      // Show success message
      setSuccess(`Policy for ${editingPolicy.category} updated successfully`);
      
      // Exit editing mode
      setEditingPolicy(null);
      
      // Clear success message after 3 seconds
      setTimeout(() => {
        setSuccess(null);
      }, 3000);
      
    } catch (err) {
      console.error('Error updating policy:', err);
      setError('Failed to update policy. Please try again.');
    }
  };
  
  if (policiesLoading) {
    return (
      <div className="flex items-center justify-center h-full">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
        <p className="ml-4 text-gray-700">Loading policies...</p>
      </div>
    );
  }
  
  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-semibold text-gray-800">Expense Policies</h1>
        <p className="text-sm text-gray-500">
          {isManager 
            ? "Set spending limits and guidelines for expense categories" 
            : "View spending limits and guidelines for expense categories"}
        </p>
      </div>
      
      {/* Error message */}
      {error && (
        <div className="bg-red-50 border-l-4 border-red-500 p-4 rounded-md">
          <div className="flex">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-red-500" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm text-red-700">{error}</p>
            </div>
          </div>
        </div>
      )}
      
      {/* Success message */}
      {success && (
        <div className="bg-green-50 border-l-4 border-green-500 p-4 rounded-md">
          <div className="flex">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm text-green-700">{success}</p>
            </div>
          </div>
        </div>
      )}
      
      {/* Policies table */}
      <div className="bg-white shadow-md rounded-lg overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Category
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Maximum Amount
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Currency
              </th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Description
              </th>
              {isManager && (
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Actions
                </th>
              )}
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {policies.map(policy => (
              <tr key={policy.category}>
                {editingPolicy && editingPolicy.category === policy.category ? (
                  // Editing row
                  <>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {policy.category}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <input
                        type="number"
                        min="0"
                        step="10"
                        value={editingPolicy.maxAmount}
                        onChange={(e) => 
                          setEditingPolicy({ 
                            ...editingPolicy, 
                            maxAmount: parseFloat(e.target.value) || 0 
                          })
                        }
                        className="form-input w-32 py-1"
                      />
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <select
                        value={editingPolicy.currency}
                        onChange={(e) => 
                          setEditingPolicy({ 
                            ...editingPolicy, 
                            currency: e.target.value as Currency 
                          })
                        }
                        className="form-select w-24 py-1"
                      >
                        {currencies.map(curr => (
                          <option key={curr} value={curr}>{curr}</option>
                        ))}
                      </select>
                    </td>
                    <td className="px-6 py-4">
                      <input
                        type="text"
                        value={editingPolicy.description}
                        onChange={(e) => 
                          setEditingPolicy({ 
                            ...editingPolicy, 
                            description: e.target.value 
                          })
                        }
                        className="form-input w-full py-1"
                      />
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <button
                        onClick={handleSave}
                        className="text-primary hover:text-indigo-700 mr-4"
                      >
                        Save
                      </button>
                      <button
                        onClick={handleCancel}
                        className="text-gray-500 hover:text-gray-700"
                      >
                        Cancel
                      </button>
                    </td>
                  </>
                ) : (
                  // Display row
                  <>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {policy.category}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {policy.maxAmount.toFixed(2)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {policy.currency}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-500">
                      {policy.description}
                    </td>
                    {isManager && (
                      <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                        <button
                          onClick={() => handleEdit(policy)}
                          className="text-primary hover:text-indigo-700"
                          disabled={editingPolicy !== null}
                        >
                          Edit
                        </button>
                      </td>
                    )}
                  </>
                )}
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      
      {/* Policy information */}
      <div className="bg-gray-50 p-6 rounded-lg shadow-sm border border-gray-200">
        <h2 className="text-lg font-medium text-gray-800 mb-4">About Expense Policies</h2>
        <p className="text-gray-600 mb-4">
          Expense policies define the maximum spending limits for each expense category. 
          These limits are used to validate expenses during submission and can help control company spending.
        </p>
        <ul className="list-disc pl-5 text-gray-600 space-y-2">
          <li>Set reasonable limits based on typical expenses in each category</li>
          <li>Provide clear descriptions to help employees understand what&apos;s covered</li>
          <li>Expenses exceeding limits will be flagged for review</li>
          {isManager && <li>You can update these policies at any time based on company needs</li>}
        </ul>
      </div>
      
      {/* Industry Spending Trends Section */}
      <div className="bg-white shadow-md rounded-lg overflow-hidden mb-6">
        <div className="bg-gradient-to-r from-indigo-600 to-indigo-800 p-4">
          <h2 className="text-lg font-semibold text-white">Industry Spending Trends - India</h2>
          <p className="text-sm text-indigo-100">
            Compare your company&apos;s spending with industry averages across India&apos;s corporate sector
          </p>
        </div>
        
        {isLoadingTrends ? (
          <div className="flex items-center justify-center p-12">
            <div className="animate-spin rounded-full h-10 w-10 border-t-2 border-b-2 border-indigo-500"></div>
            <p className="ml-4 text-gray-600">Loading latest industry trend data...</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Expense Category
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Industry Average (Monthly)
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Your Company&apos;s Avg Spend
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Difference
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Market Trend
                  </th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Insights
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {industryTrends.map(trend => (
                  <tr key={trend.category}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {trend.category}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {trend.industryAverage.toLocaleString('en-IN')}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {trend.companyAverage.toLocaleString('en-IN')}
                    </td>
                    <td className={`px-6 py-4 whitespace-nowrap text-sm ${trend.difference > 0 ? 'text-red-500' : 'text-green-500'}`}>
                      {trend.difference > 0 ? '+' : ''}{trend.difference.toLocaleString('en-IN')} 
                      ({trend.percentageDifference > 0 ? '+' : ''}{trend.percentageDifference.toFixed(1)}%)
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm">
                      <span className={`px-2 py-1 rounded-full text-xs ${
                        trend.trend.includes('up') 
                          ? 'bg-red-100 text-red-800' 
                          : trend.trend.includes('down')
                            ? 'bg-green-100 text-green-800'
                            : 'bg-gray-100 text-gray-800'
                      }`}>
                        {trend.trend}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-700">
                      {trend.insight}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
        
        <div className="bg-gray-50 px-4 py-3">
          <div className="flex items-center justify-between">
            <p className="text-xs text-gray-500">
              Data based on real industry benchmarks from across India&apos;s corporate sector.
            </p>
            <button 
              onClick={() => fetchIndustryData()}
              className="text-xs text-indigo-600 hover:text-indigo-800 font-medium"
            >
              Refresh Data
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

// Set displayName for component
ExpensePolicy.displayName = 'ExpensePolicy';

export default ExpensePolicy;